.navbar {
    width: 150px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.pictures {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
}

.img {
    width: 20px;
    height: 20px;
    cursor: pointer;
}